/*** CrankWheel's font library ***/

/*** Instructions ***
 * Uncomment fonts you need to use in your design.
 */

/*** Headings ***/
/* Titillium Web 200 - Thin */
@font-face {
    font-family: 'Titillium Web';
    font-style: normal;
    font-weight: 200;
    src: local('Titillium WebThin'),
         local('Titillium-WebThin'),
         url(../fonts/TitilliumWeb-ExtraLight.ttf) format('truetype');
}

/* Titillium Web 300 - Light */
@font-face {
    font-family: 'Titillium Web';
    font-style: normal;
    font-weight: 300;
    src: local('Titillium WebLight'),
         local('Titillium-WebLight'),
         url(../fonts/TitilliumWeb-Light.ttf) format('truetype');
}

/* Titillium Web 400 - Regular */
@font-face {
    font-family: 'Titillium Web';
    font-style: normal;
    font-weight: 400;
    src: local('Titillium Web'),
         local('Titillium-WebRegular'),
         url(../fonts/TitilliumWeb-Regular.ttf) format('truetype');
}

/*** Body text ***/
/* Lato 300 - Light */
@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 300;
    src: local('Lato Light'),
         local('Lato-Light'),
         url(../fonts/Lato-Light.ttf) format('truetype');
}

/* Lato 400 - Regular */
@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    src: local('Lato Regular'),
         local('Lato-Regular'),
         url(../fonts/Lato-Regular.ttf) format('truetype');
}

/* Lato 700 - Bold */
@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 700;
    src: local('Lato Bold'),
         local('Lato-Bold'),
         url(../fonts/Lato-Bold.ttf) format('truetype');
}

/* Material Icons */
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/MaterialIcons-Regular.eot'); /* For IE6-8 */
  src: local('Material Icons'),
       local('MaterialIcons-Regular'),
       url('../fonts/MaterialIcons-Regular.woff2') format('woff2'),
       url('../fonts/MaterialIcons-Regular.woff') format('woff'),
       url('../fonts/MaterialIcons-Regular.ttf') format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;

  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}
