@import 'variables';

body {
    font-size: 1em;
    line-height: 1.375em;
    font-family: $cw-body;
}

p {
    font-size: 1em;
}

blockquote {
    font-size: 1.5em;
    line-height: 1.45em;
}

h1, h2, h3, h4, h5, h6 {
    font-family: $cw-heading;
    margin: 1.4em 0 .8em 0;
}

h1 {
    font-weight: 200;
    font-size: 2.8em;
    line-height: 1.2em;
}

h2 {
    font-weight: 200;
    font-size: 2.25em;
    line-height: 1.25em;
}

h3 {
    font-weight: 300;
    font-size: 1.75em;
    line-height: 1.25em;
}

h4 {
    font-weight: 300;
    font-size: 1.4em;
    line-height: 1.25em;
}

h5 {
    font-weight: 400;
    font-size: 1.125em;
    line-height: 1.35em;
}

h6 {
    font-weight: 400;
    font-size: .8em;
    line-height: 1.2em;
}

@media only screen and (max-width: 768px) {
    h1 {
        font-size: 2.4em;
        line-height: 1.125em;
    }

    h2 {
        font-size: 2em;
    }

    h3 {
        font-size: 1.5em;
    }

    h4 {
        font-size: 1.25em;
        line-height: 1.135em;
    }
}

@media only screen and (max-width: 420px) {
    body {
        line-height: 1.25em;
    }

    blockquote {
        font-size: 1.25em;
        line-height: 1.25em;
    }

    h1 {
        font-size: 1.8em;
        line-height: 1.25em;
    }

    h2 {
        font-size: 1.625em;
        line-height: 1.15em;
    }

    h3 {
        font-size: 1.375em;
        line-height: 1.135em;
    }

    h5 {
        line-height: 1.3em;
    }
}
