@import 'mixin/flex/flex';
@import 'bourbon';
@import 'cw/variables';
@import 'cw/common';

nav {
    @include background($bg-nav-top);
    font-size: 1rem;

    .nav-top {
        @include flexbox();
        @include align-items(center);
        @include justify-content(space-between);

        * {
            margin: 0 15px;
        }

        .nav-logo {
            height: 30px;

            &.full {
                height: 40px;
                margin: 15px;
            }
        }

        .nav-header {
            @include flex-grow(1);
            font-family: $cw-heading;
            font-size: 1.2em;
            color: $primary-light;
            margin-left: 0;
        }

        .nav-user {
            font-size: .9em;
            color: $secondary-light;
        }
    }

    ul {
        max-width: $site-max-width;
        margin: 0 auto;
        color: $secondary-light;

        li {
            display: inline-block;
            list-style: none;
            margin: 0 10px;
            padding: 10px 5px;
            font-size: .9em;
            cursor: pointer;

            &.active {
                color: $cw-orange-dark;
                box-shadow: inset 0 -4px $cw-orange-dark;
                @include transition(box-shadow 300ms);
            }

            &:hover {
                color: $primary-light;
            }
        }
    }
}

main {
    max-width: $site-max-width;
    margin: 0 auto;
    padding: 25px;
    @include flexbox();
    @include justify-content(center);
}

.nav-side {
    @include flex(0 0 25%);
    color: $secondary-dark;
    margin: 0;
    padding: 0;

    li {
        list-style: none;
        margin: 20px 15px;
        padding-left: 15px;
        cursor: pointer;

        &.active {
            color: $primary-dark;
            box-shadow: inset 2px 0 $primary-dark;
            @include transition(box-shadow 300ms);
        }

        &:hover {
            color: $primary-dark;
        }
    }
}

.box {
    @include flex(1);
    @include background($bg-box);
    border: 1px solid $divider-dark;
    border-radius: 2px;

    .box-heading {
        @include background($bg-body);
        border-bottom: 1px solid $divider-dark;
        color: $primary-dark;
        padding: .8em 1.6em;

        h4 {
            margin: 0;
        }
    }

    .box-content {
        padding: 20px;
    }

    button[type=submit] {
        margin-top: 20px;
    }
}

.hero {
    display: none;
    @include flex(0 1 500px);
    font-size: 1rem;

    &:target, &.show {
        display: block;
        @include animation(fadein 200ms ease);
        @include keyframes(fadein) {
            from {
                opacity: 0;
                @include transform(scale(.95));
            }
            to {
                opacity: 1;
                @include transform(scale(1));
            }
        }
    }

    h5, h6, p {
        color: $secondary-dark;
    }

    .input-container:first-of-type {
        margin-top: 30px;
    }

    .button-group {
        @include flexbox();
        @include justify-content(center);
        margin-top: 40px;

        button {
            margin: 0 5px;
            @include flex-basis(250px);
            @include flex-shrink(1);
        }

        @media only screen and (max-width: 360px) {
            & {
                @include flex-wrap(wrap);
            }

            button {
                @include flex(1 1 100%);
                margin-bottom: 10px;
            }
        }
    }

    strong {
        color: $primary-dark;
    }

    &#password {
        #field-signup-mailinglist:checked ~ .input-info {
            display: none;
        }
    }

    &#lobby {
        .input-container > label {
            font-size: 1.2em;
            padding-top: .6em;
            padding-right: .5em;
            margin-bottom: .5em;
            color: $input-dark;

            @media only screen and (max-width: 768px) {
                &  {
                    text-align: center;
                }
            }
        }

        p.lobby-suggestions {
            margin-bottom: .4em;
            font-size: .9em;

            strong {
                color: $input-dark;
            }
        }

        ul.lobby-suggestions {
            cursor: pointer;
            list-style: none;
            padding: 0;
            margin: 0;
            font-size: 1em;
            color: $secondary-dark;
        }

        #field-signup-lobby-request {
            color: $primary-dark;
        }
    }

    &#invite {
        #signup-invite-skip {
            @include flex(1);
        }
    }

    &#finish {
        button {
            @include flex(1);
        }
    }
}
