@import '../mixin/flex/flex';
@import 'bourbon';
@import '../mixin/cw/cw';
@import 'normalize';
@import 'fonts';
@import 'typography';
@import 'variables';

.hide {
    display: none !important;
}

html {
}

body {
    padding: 0;
    color: $primary-dark;
    @include background($bg-body);
}

a.link {
    color: $cw-orange-light;
    font-weight: bolder;
    text-decoration: none;
    cursor: pointer;

    &:hover {
        text-decoration: underline;
    }
}

span {
    &.success {
        color: $cw-green;
        font-weight: 700;
    }
}

button, .button {
    @include appearance(none);
    @include background($button-light);
    outline: 0;
    border: 1px solid $divider-dark;
    border-radius: 2px;
    display: table;
    padding: .9em 1.8em;
    color: $input-dark;
    font-size: .9em;
    font-weight: 700;
    text-decoration: none;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    @include transition(background 200ms);

    span {
        vertical-align: middle;
    }

    &:hover {
        @include background($button-light-hover);
        text-decoration: none;
    }

    &:active {
        box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.12);
    }

    &:disabled {
        pointer-events: none;
        opacity: .5;
    }

    &.primary {
        @include background($cw-orange-light);
        color: $primary-light;
        border: 0;

        &:hover {
            @include background($cw-orange-dark);
        }

        &:disabled {
            @include background($disabled-dark);
        }
    }

    &.outline {
        @include background(none);
        border: 1px solid $disabled-dark;
        color: $button-dark;

        &:hover {
            @include background($button-light);
        }
    }

    &.minimal {
        @include background(none);
        color: $button-dark;
        border: 0;
        padding: 0;

        &:hover {
            @include background(none);
            color: $cw-orange-light;
        }

        &:active {
            box-shadow: none;
        }
    }

    &.inline {
        display: inline-block;
        @include background(none);
        color: $button-dark;
        padding: 0;
        border: 0;

        &:hover {
            @include background(none);
            color: $cw-orange-light;

            .loading-spinner {
                border-top: 2px solid $cw-orange-light;
            }
        }

        .loading-spinner {
            border-top: 2px solid $primary-dark;

            &.show {
                @include inline-flex();
            }
        }
    }

    &.light {
        color: $primary-light;
        border-color: $primary-light;
    }

    &.icon-right {
        span:first-of-type {
            margin-left: 24px;
            margin-right: 12px;
        }
    }

    &.small {
        font-size: .7em;
        padding: .7em 1.4em;
    }

    &.large {
        font-size: 1.1em;
        padding: 1.1em 2.2em;
    }

    .loading-spinner {
        display: none;
        margin: 3px;
        border: 2px solid transparent;
        border-top: 2px solid $primary-light;
        border-radius: 50%;
        @include size(14px);
        @include animation(spin 1s ease infinite);
        @include keyframes(spin) {
            from {
                @include transform(rotate(0deg));
            }
            to {
                @include transform(rotate(360deg));
            }
        }

        &.show {
            display: inline-block;
        }
    }
}

.input-container {
    @include flexbox();
    @include flex-flow(row wrap);
    margin-bottom: 20px;

    &.stacked {
        @include flex-flow(row no-wrap);

        label {
            @include flex(1 0 100%);
            text-align: left;
        }
    }

    input {
        @include flex(1);
        outline: 0;
        box-shadow: 0;
        height: 1.4em;
        line-height: 1.4em;
        padding: .6em;
        font-size: 1.1em;
        color: $input-dark;
        border: 1px solid $divider-dark;
        border-radius: 2px;
        @include background(#fff);

        @include placeholder {
            font-weight: 300;
            font-size: .9em;
            text-shadow: none;
            color: $disabled-dark;
        }

        &:focus {
            outline: none;
            border: 1px solid $cw-orange-light;
            box-shadow: 0 0 1px 0 $cw-orange-light;
        }

        &.loading {
            @include input-bg(url('/ss/images/cwloading-orange.gif'));
        }

        &.success {
            @include input-bg(url('/ss/images/cwsuccess.gif'));
            box-shadow: none;
        }
    }

    label {
        @include flex(0 1 20%);
        display: inline-block;
        padding-top: .5em;
        padding-right: 1.5em;
        padding-bottom: .3em;
        font-size: .9em;
        text-align: right;
        color: $secondary-dark;

        @media only screen and (max-width: 768px) {
            & {
                text-align: left;
                @include flex(1 0 100%);
            }
        }
    }

    .input-group {
        @include flex(1);
        @include flexbox();
        @include flex-flow(row wrap);

        &.control {
            display: block;
        }

        .input-info {
            @include flex(1 0 100%);
            font-size: .7em;
            line-height: 1.5em;
            padding-top: .5em;
            padding-left: .2em;
            color: $disabled-dark;

            &.active {
                color: $secondary-dark;
                @include transition(color 200ms);
            }

            &.error {
                display: none;
                font-weight: 700;
                color: $cw-red;

                &.show {
                    display: block;
                }
            }
        }

        .input-control {
            display: block;
            position: relative;
            padding: 0;
            padding-left: 35px;
            cursor: pointer;
            font-size: 1em;
            color: $input-dark;

            @media only screen and (max-width: 360px) {
                & {
                    font-size: .8em;
                    line-height: 1.5em;
                }
            }

            input {
                position: absolute;
                z-index: -1;
                opacity: 0;

                &:checked ~ .control {
                    @include background($cw-orange-light);
                }

                &:checked ~ .control:after {
                    display: block;
                }

                &:disabled ~ .control {
                    pointer-events: none;
                    opacity: .6;
                    @include background($disabled-dark);
                }
            }

            .control {
                position: absolute;
                top: 2px;
                left: 0;
                @include size(20px);
                @include background($divider-dark);

                &:after {
                    position: absolute;
                    display: none;
                    content: '';
                }
            }

            &.checkbox {
                .control:after {
                    top: 4px;
                    left: 8px;
                    @include size(3px 8px);
                    @include transform(rotate(45deg));
                    border: solid $primary-light;
                    border-width: 0 2px 2px 0;
                }
            }

            &.radio {
                margin-bottom: 10px;

                .control {
                    border-radius: 50%;

                    &:after {
                        @include background($primary-light);
                        @include size(6px);
                        border-radius: 50%;
                        top: 7px;
                        left: 7px;
                    }
                }

                input:disabled ~ .control {
                    @include background($disabled-dark);
                }
            }
        }
    }
}

p {
    &.small {
        font-size: .7em;
        line-height: 1.3em;
        color: $secondary-dark;
    }
}
