/* Fonts */
$cw-heading: 'Titillium Web', sans-serif;
$cw-body: 'Lato', sans-serif;

/* Brand colors */
$cw-orange-dark: #fcb42e;
$cw-orange-light: #f99c1d;
$cw-blue: #39bfbf;
$cw-green: #009000;
$cw-red: #c30000;

/* Font and icon colors */
$primary-dark: rgba(0,0,0,.87);
$input-dark: rgba(0,0,0,.7);
$secondary-dark: rgba(0,0,0,.54);
$disabled-dark: rgba(0,0,0,.38);
$divider-dark: rgba(0,0,0,.12);
$icon-active-dark: rgba(0,0,0,.54);
$icon-inactive-dark: rgba(0,0,0,.38);

$primary-light: rgba(255,255,255,1);
$input-light: rgba(255,255,255,1);
$secondary-light: rgba(255,255,255,.7);
$disabled-light: rgba(255,255,255,.5);
$divider-light: rgba(255,255,255,.12);
$icon-active-light: rgba(255,255,255,1);
$icon-inactive-light: rgba(255,255,255,.5);

/* Background colors */
$bg-nav-top: #212121;
$bg-body: #fafafa;
$bg-box: #ffffff;

/* Button colors */
$button-dark: rgba(0,0,0,.7);
$button-light: rgba(0,0,0,.04);
$button-light-disabled: rgba(0,0,0,.02);
$button-light-hover: rgba(0,0,0,.08);

/* Proportions */
$site-max-width: 1080px;
